import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";

import Timeline from "@material-ui/lab/Timeline";
import TimelineItem from "@material-ui/lab/TimelineItem";
import TimelineSeparator from "@material-ui/lab/TimelineSeparator";
import TimelineConnector from "@material-ui/lab/TimelineConnector";
import TimelineContent from "@material-ui/lab/TimelineContent";
import TimelineOppositeContent from "@material-ui/lab/TimelineOppositeContent";
import TimelineDot from "@material-ui/lab/TimelineDot";
import FastfoodIcon from "@material-ui/icons/Fastfood";
import LaptopMacIcon from "@material-ui/icons/LaptopMac";
import HotelIcon from "@material-ui/icons/Hotel";
import RepeatIcon from "@material-ui/icons/Repeat";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import aboutimage from "../Images/IMG-20191227-WA0023.jpg";
import Aos from "aos";
import "aos/dist/aos.css";
import Typewriter from "typewriter-effect";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";

import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import "./Timelinepage.css";
const useStyles = makeStyles((theme) => ({
  paper: {
    padding: "6px 16px",
  },
  secondaryTail: {
    backgroundColor: theme.palette.secondary.main,
  },
}));

export default function Timelinepage() {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const [dataa, setdataa] = useState(false);
  const [trigger, settrigger] = useState(false);
  const changeback = () => {
    if (window.scrollY >= 1500 && window.scrollY <= 2500) {
      settrigger(true);
    } else {
      settrigger(false);
    }
  };
  window.addEventListener("scroll", changeback);
  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
    setdataa(isExpanded ? true : false);
  };
  useEffect(() => {
    Aos.init({
      duration: 3000,
      delay: 300,
    });
  }, []);
  return (
    <div id="timeline" className="Timelinepage">
      <div style={{ fontSize: 40, fontWeight: 900 }}>
        <Typewriter
          options={{
            strings: ["Timeline"],
            cursor: "",
            delay: 100,
            autoStart: trigger,
            pauseFor: 10000000000,
            loop: true,
          }}
        />
      </div>
      <Timeline align="alternate">
        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2" color="textSecondary">
              2017
            </Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <img
              src={aboutimage}
              style={{ height: "40px", width: "40px", borderRadius: "50%" }}
            />

            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent data-aos="fade-left">
            <Accordion
              expanded={expanded === "panel1"}
              onChange={handleChange("panel1")}
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography className={classes.heading}>
                  Dil Hai Hindustani Season 1
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typewriter
                  options={{
                    strings: [
                      "Anim mollit aliqua laboris elit. Anim mollit aliqua laboris elit. Anim mollit aliqua laboris elit.",
                    ],
                    cursor: "",
                    delay: 100,
                    autoStart: dataa,
                    pauseFor: 10000000000,
                    loop: true,
                  }}
                />
              </AccordionDetails>
            </Accordion>
          </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2" color="textSecondary">
              2018
            </Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color="primary">
              <LaptopMacIcon />
            </TimelineDot>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent data-aos="fade-right">
            <Accordion
              expanded={expanded === "panel2"}
              onChange={handleChange("panel2")}
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography className={classes.heading}>
                  Dil Hai Hindustani Season 2
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typewriter
                  options={{
                    strings: [
                      "Anim mollit aliqua laboris elit. Anim mollit aliqua laboris elit. Anim mollit aliqua laboris elit.",
                    ],
                    cursor: "",
                    delay: 100,
                    autoStart: dataa,
                    pauseFor: 10000000000,
                    loop: true,
                  }}
                />
              </AccordionDetails>
            </Accordion>
          </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2" color="textSecondary">
              2017
            </Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color="primary" variant="outlined">
              <HotelIcon />
            </TimelineDot>
            <TimelineConnector className={classes.secondaryTail} />
          </TimelineSeparator>
          <TimelineContent data-aos="fade-left">
            <Accordion
              expanded={expanded === "panel3"}
              onChange={handleChange("panel3")}
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography className={classes.heading}>ARRived</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typewriter
                  options={{
                    strings: [
                      "Anim mollit aliqua laboris elit. Anim mollit aliqua laboris elit. Anim mollit aliqua laboris elit.",
                    ],
                    cursor: "",
                    delay: 100,
                    autoStart: dataa,
                    pauseFor: 10000000000,
                    loop: true,
                  }}
                />
              </AccordionDetails>
            </Accordion>
          </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2" color="textSecondary">
              2017
            </Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color="primary" variant="outlined">
              <HotelIcon />
            </TimelineDot>
            <TimelineConnector className={classes.secondaryTail} />
          </TimelineSeparator>
          <TimelineContent data-aos="fade-right">
            <Accordion
              expanded={expanded === "panel4"}
              onChange={handleChange("panel4")}
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography className={classes.heading}>ARRived</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typewriter
                  options={{
                    strings: [
                      "Anim mollit aliqua laboris elit. Anim mollit aliqua laboris elit. Anim mollit aliqua laboris elit.",
                    ],
                    cursor: "",
                    delay: 100,
                    autoStart: dataa,
                    pauseFor: 10000000000,
                    loop: true,
                  }}
                />
              </AccordionDetails>
            </Accordion>
          </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2" color="textSecondary">
              2017
            </Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color="primary" variant="outlined">
              <HotelIcon />
            </TimelineDot>
            <TimelineConnector className={classes.secondaryTail} />
          </TimelineSeparator>
          <TimelineContent data-aos="fade-left">
            <Accordion
              expanded={expanded === "panel5"}
              onChange={handleChange("panel5")}
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography className={classes.heading}>ARRived</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                  Suspendisse malesuada lacus ex, sit amet blandit leo lobortis
                  eget.
                </Typography>
              </AccordionDetails>
            </Accordion>
          </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent>
            <Typography variant="body2" color="textSecondary">
              2017
            </Typography>
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color="secondary">
              <RepeatIcon />
            </TimelineDot>
          </TimelineSeparator>
          <TimelineContent data-aos="fade-right">
            <Accordion
              expanded={expanded === "panel6"}
              onChange={handleChange("panel6")}
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography className={classes.heading}>Accordion 1</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                  Suspendisse malesuada lacus ex, sit amet blandit leo lobortis
                  eget.
                </Typography>
              </AccordionDetails>
            </Accordion>
          </TimelineContent>
        </TimelineItem>
      </Timeline>
    </div>
  );
}
