import React, { useState } from "react";
import emailjs from "emailjs-com";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import { Grid, Button } from "@material-ui/core";
import Typewriter from "typewriter-effect";
import Alert from "@material-ui/lab/Alert";
import IconButton from "@material-ui/core/IconButton";
import Collapse from "@material-ui/core/Collapse";

import CloseIcon from "@material-ui/icons/Close";
import "./Contact.css";
function Contact() {
  function sendEmail(e) {
    e.preventDefault();

    emailjs
      .sendForm(
        "gmail",
        "template_yvag63b",
        e.target,
        "user_x3GgbTixvbsPLNYyPT5dJ"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
    // alert("Thanky for Connecting me");
  }
  const [open, setOpen] = React.useState(false);
  const [trigger, settrigger] = useState(false);
  const changeback = () => {
    if (window.scrollY >= 1000 && window.scrollY <= 1500) {
      settrigger(true);
    } else {
      settrigger(false);
    }
  };
  window.addEventListener("scroll", changeback);
  return (
    <div className="Contact" id="contact">
      <div style={{ fontSize: 40, fontWeight: 900 }}>
        <Typewriter
          options={{
            strings: ["Contact Me"],
            cursor: "",
            delay: 100,
            autoStart: trigger,
            pauseFor: 10000000000,
            loop: true,
          }}
        />
      </div>

      <form className="contact-form" onSubmit={sendEmail}>
        <Grid
          container
          xs={12}
          spacing={2}
          item
          direction="row"
          justify="center"
          alignItems="center"
        >
          <Grid item lg={6} sm={6} xs={12}>
            <TextField
              color="secondary"
              className="Input"
              type="text"
              name="contact_number"
              label="Contact Number"
            />
          </Grid>
          <Grid item lg={6} sm={6} xs={12}>
            <TextField
              color="secondary"
              className="Input"
              type="text"
              name="user_name"
              label="Name"
            />
          </Grid>
          <Grid item lg={6} sm={6} xs={12}>
            <TextField
              color="secondary"
              className="Input"
              type="email"
              name="user_email"
              label="Email"
            />
          </Grid>
          <Grid item lg={6} sm={6} xs={12}>
            <TextField
              color="secondary"
              className="Input"
              name="message"
              label="Message"
            />
          </Grid>
          <br></br>
          <br></br>
          <br></br>
          <br></br>
          <Grid item lg={12} sm={12} xs={12} style={{ paddingRight: "0" }}>
            <Button
              disabled={open}
              variant="outlined"
              onClick={() => {
                setOpen(true);
              }}
              type="submit"
              value="Send"
              variant="contained"
              color="secondary"
            >
              Submit
            </Button>
          </Grid>
        </Grid>
      </form>
      <Collapse in={open}>
        <Alert
          action={
            <IconButton
              aria-label="close"
              color="inherit"
              size="small"
              onClick={() => {
                setOpen(false);
              }}
            >
              <CloseIcon fontSize="inherit" />
            </IconButton>
          }
        >
          Thankyou for connecting me !
        </Alert>
      </Collapse>
    </div>
  );
}

export default Contact;
